 .contenedorCarta{
     padding: 40px;
 }
.carta{
    overflow: hidden;
    width: 250px;
    height: 350px;
    position: relative;
    margin:  auto;
    box-shadow: 30px 23px 6px -7px rgba(14, 13, 13, 0.925);
-webkit-box-shadow: 30px 23px 6px -7px rgba(14, 13, 13, 0.925);
-moz-box-shadow: 30px 23px 6px -7px rgba(14, 13, 13, 0.925);
border-radius: 20px;
-webkit-border-radius: 20px;
-moz-border-radius: 20px;
-ms-border-radius: 20px;
-o-border-radius: 20px;
        &__img{
            width: inherit;
            height: inherit;
            border-radius: 20px;
            -webkit-border-radius: 20px;
            -moz-border-radius: 20px;
            -ms-border-radius: 20px;
            -o-border-radius: 20px;
        }
        &__precio{
            width: 80px;
            height: 80px;
            position: absolute;
            top: -16px;
            right: -6px;
            display: flex;
            text-align: center;
            align-items: center;
            justify-content: center;
            background-color: red;
            border-radius: 50%;
            -webkit-border-radius: 50%;
                span{
                    font-size: 1.6em;
                    color: #fff;
                }
    }
}

.btnYinfo{
    width: 100%;
    height: 50%;
    position: absolute;
    bottom: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    border-radius: 5px;
            background-color: rgba(0, 0, 0, 0.575);
            color: rgb(255, 255, 255);
        &__info{
            font-size: 1.2em;
            font-weight: 600;
            text-align: center;
        }
        &__btn > button{
                background-color: rgb(255, 0, 0);
                border: none;
                width: 120px;
                height: 30px;
                border-radius: 5px;
                -webkit-border-radius: 5px;
                -moz-border-radius: 5px;
                -ms-border-radius: 5px;
                -o-border-radius: 5px;
                color: #fff;
                font-weight: 750;
                cursor: pointer;
    }
}

.modal{
    opacity: 0;
    background-color: rgba(0, 0, 0, 0.589);
    width: 100vw;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    z-index: -1;
    display: flex;
    justify-content: center;
    align-items: center;
    transition:1s ease;
    .modalInfo   {
            transform: translateY(-200%);
            overflow: hidden;
            position: relative;
            width: 350px;
            height: 520px;
            background-color: #fff;
            text-align: center;
            border-radius: 5px;
            -webkit-border-radius: 5px;
            -moz-border-radius: 5px;
            -ms-border-radius: 5px;
            -o-border-radius: 5px;
            transition: .5s ease;
            -webkit-transition: .5s ease;
            -moz-transition: .5s ease;
            -ms-transition: .5s ease;
            -o-transition: .5s ease;
            &.mostrarInfo
            {
                transition: transform .8s ease-out;
                transform: translateY(0);
                -webkit-transform: translateY(0);
                -moz-transform: translateY(0);
                -ms-transform: translateY(0);
                -o-transform: translateY(0);
                -webkit-transition: transform .8s ease-out;
                -moz-transition: transform .8s ease-out;
                -ms-transition: transform .8s ease-out;
                -o-transition: transform .8s ease-out;
                transition-property: all;
                transition-delay:  .8s;
}

            &__titulo {font-size: 1.5em;}
                &__beneficios{
                    list-style: none;
                    text-align: center;
                    padding: 0;
                    font-size: 18px;
                        li{line-height: 2; margin: 20px 0;}
                }
            }
    }.modalInfo.brillante{
        background: #ffb347;  /* fallback for old browsers */
background: -webkit-linear-gradient(to right, #ffcc33, #ffb347);  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to right, #ffcc33, #ffb347); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */


    }
       .modalBtn__btn{
        cursor: pointer;
           font-size: 1.2em;
        background-color: rgb(255, 0, 0);
        border: none;
        width: 160px;
        height: 50px;
        border-radius: 5px;
        -webkit-border-radius: 5px;
        -moz-border-radius: 5px;
        -ms-border-radius: 5px;
        -o-border-radius: 5px;
        color: #fff;
        font-weight: 750;
       }
    .modalCerrar{
        cursor: pointer;
        display: flex;
        justify-content: center;
        align-items: center;
        position: absolute;
        top: -10px;
        right: -5px;
        width: 45px;
        height: 45px;
        background-color: red;
        border-radius: 50%;
        -webkit-border-radius: 50%;
        -moz-border-radius: 50%;
        -ms-border-radius: 50%;
        -o-border-radius: 50%;
}

.modalActivo{
    transition: opacity .6s ease-out;
    opacity: 1;
    z-index: 100;
    -webkit-transition: opacity .6s ease-out;
    -moz-transition: opacity .6s ease-out;
    -ms-transition: opacity .6s ease-out;
    -o-transition: opacity .6s ease-out;
   
}
.modalIcons{ margin-right: 5px;}


